<template>
    <v-app>
    <v-navigation-drawer
      fixed
      v-model="drawerRight"
      right
      clipped
      app
    >

    <div>대화상대</div>
    <hr>
    </v-navigation-drawer>
    <v-toolbar
      color="blue"
      dark
      fixed
      app
      clipped-right
    >
        <v-btn icon>
        <v-icon @click="home">arrow_back</v-icon>
        </v-btn>
      <v-toolbar-title>Chat Room</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-side-icon @click.stop="drawerRight = !drawerRight"></v-toolbar-side-icon>
    </v-toolbar>
    
   
    <v-content>
      <v-container fluid fill-height>
        <v-layout justify-center align-center> 


        <div class="chat" id="messages">
       
            <v-list two-line subheader :class="isMine(message, index)" v-for="(message, index) in messages" :key="message.id">
                <span v-if="addDateDivider(message, index)" id="dateDivider">{{ addDateDivider(message, index) }}</span>
                <span id="name">{{ message.name }}&lrm;</span>                
                <span id="content">{{message.content}}&lrm;</span>
                <span id="time">{{ changeFormat(message.timestamp) }}</span>     
            </v-list>
      
        </div>   
        </v-layout>
      </v-container>
    </v-content>

  <v-footer app>
        <div class="sendMessage">
            <form @submit.prevent="addMessage">          
            <v-text-field
              append-outer-icon="send"
              name="new-message" 
              v-model="newMessage" 
              autocomplete="off" 
              @click:append-outer="addMessage"
              id="inputMessage"
            ></v-text-field>
        </form>
        </div>
    </v-footer> 
  </v-app>
</template>

<script>
import db from '@/firebase/init'
import moment from 'moment'

export default {
    name: 'Chat',
    props: ['name'],
    data () {
        return {
            messages: [],
            newMessage: null,
            drawerRight: false,
            right: null,
            dateDivider: null
        }
    },
    methods: {
        addMessage(){
            if(this.newMessage){
                db.collection('messages').add({
                    content: this.newMessage,
                    name: this.name,
                    timestamp: Date.now()
                }).catch(err => {
                    console.log(err)
                })
                this.newMessage = null
            }
            document.getElementById("inputMessage").focus();
        },
        sendMessage(){
            this.addMessage()
        },
        isMine(message, index){
            var messageClass = '';
            
            if(message.name == this.name) messageClass += 'from_me';
            else messageClass += 'from_you'; 

            if(index > 0){
                if(message.name == this.messages[index-1].name){
                    messageClass += ' sameSender';                  
                }                    
            }
                        
            if(index < this.messages.length-1){
                if(moment(message.timestamp).format('LT') == moment(this.messages[index+1].timestamp).format('LT')
                    && message.name == this.messages[index+1].name) messageClass += ' sameTime'; 
            } 
            return messageClass;            
        },
        addDateDivider(message, index){
            if(index == 0){
                return moment(message.timestamp).format('LL');
            }
            else{
                if(index < this.messages.length){
                    if(moment(message.timestamp).format('LL') != moment(this.messages[index-1].timestamp).format('LL')) return moment(message.timestamp).format('LL');
                }
            }
        },
        changeFormat(timestamp){
            return moment(timestamp).format('LT');
        },
        home(){
            this.$router.push({ name: 'Welcome' })
        }
    },
    created(){
        let ref = db.collection('messages').orderBy('timestamp')
    console.log(ref);
        ref.onSnapshot(snapshot => {
            snapshot.docChanges().forEach(change => {
                if(change.type == 'added'){
                    let doc = change.doc
                    this.messages.push({
                        id: doc.id,
                        name: doc.data().name,
                        content: doc.data().content,
                        timestamp: doc.data().timestamp
                    })
                }
            })
        })        
        var el = document.getElementById('messages'); 
        el.scrollTop = el.scrollHeight;
    },
    updated(){
        var el = document.getElementById('messages'); 
        el.scrollTop = el.scrollHeight;        
    }
}
</script>

<style>


@media (min-width: 450px) {
.chat{
    width:100%;
}

.sendMessage{
    width:100%;
    background: #fff;
    margin : auto;
}

}

.chat{
    width: 600px;
}

.sendMessage{
    width:600px;
    background: #fff;
    margin : auto;
}

.v-content__wrap{
    max-height:90%;
    overflow: auto;
}
.container.fill-height{
    background: #fff;
}

.v-input{
    width:100%;
    margin:auto;
}

#dateDivider{
    display: block;
    text-align: center;
    margin: 10px;
    font-weight: bold;
    font-style: italic;
}

.from_me{
    text-align: right;
    direction: rtl;
}

.from_me #content{
    display: inline-block;
	text-align: left;
    position: relative;
    background: #3c81fa;
	word-wrap: break-word;
	max-width: 300px;
	padding:5px;
    border-radius: 10px;
}

.from_you #content{    
    display: inline-block;
    position: relative;    
	word-wrap: break-word;	
    background: #8e8e93;
	max-width: 300px;
	padding:5px;
    border-radius: 10px;
}

#name{
    font-weight: bold;
    display: block;
}

#content{
    overflow-wrap: break-word;
}

#time{
    font-size:0.8em;
}

.sameSender #name{
    display: none;
}

.sameTime #time{
    display: none;
}

span{
    margin: 0px;
    padding: 0px;
}

#messages{
    overflow: auto;
    max-height: 70vh;
}

@media (min-height: 450px) {
#messages{
    overflow: auto;
    max-height: 85vh;
}
}
/* width */
::-webkit-scrollbar {
    width: 0px;
}
/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555; 
}
</style>



