<template>
<v-app>
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs8 md4 class="text-xs-center">
              <h1>Chat App</h1>
              <v-form @submit.prevent="enterChat">
                <v-text-field name="login" type="text" label="Enter your name" v-model="name" autocomplete="off" ></v-text-field>      
                <p v-if="feedback">{{ feedback }}</p>
                <v-btn type="submit" color="primary">시작하기</v-btn>
              </v-form>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: 'Welcome',
  data () {
    return {
      name: null,
      feedback: '6글자 이하로 입력해주세요.'
    }
  },
  methods: {
    enterChat(){      
      if(this.name){
        this.$router.push({ name: 'Chat', params: { name: this.name } })
      } else {
        this.feedback = '닉네임이 있어야 합니다.'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.v-card__text{
  height:50%;
}
</style>



